@media screen and (min-width:600px) {

.containerf{
        position:absolute;
        height: 50%;
        width: 80%;
        left: 50%;
        top:50%;
        transform: translate(-50%,-50%);
        background-color: white;
    }
    .item5{
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        
    }
    .postalCode{
        font-weight: 700;
        font-size: xx-large;
        text-align: center;
        color:gray;
        height: 20%;
        width:80%;
        border:none;
        border-bottom: 2px solid black;
    }
    .inconnu{
        margin-top:5%;
        margin-left:10%;
        align-self: flex-start;
        height: 10%;
    }
    .buttonContainer{
        height: 20%;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;

    }
    .livrer{
        background-color: white;
        color: black;
        font-size: 1rem;
        font-weight: bold;
        width: 40%;
        height:40%;
    }
    .amporter{
        background-color: white;
        color: black;
        font-size: 1rem;
        font-weight: bold;
        width: 40%;
        height:40%;
    }
    .commanderContainer{
        height: 20%;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;

    }
    .commander{
        background-color: black;
        color: white;
        font-size: 1.6rem;
        width: 80%;
        height:60%;
    }
}
